import Swal from 'sweetalert2';

export const swal = () => {
    Swal.fire({
        title: "Credenciales invlidas",
        text: "Por favor introduzca credenciales vlidas",
        confirmButtonText: "Aceptar",
        width: "400px",
        timer: 10000,
        timerProgressBar: true,
      })
};